import keyboardPackage from '../../components/keyboard-package/keyboard-package.vue';
import passwordInput from '../../components/password-input/password-input.vue';
export default {
	data() {
		return {
			tabCurrentIndex: 0,
			data_loading: true,
			navList: [
				{
					state: '[101,102,103,104,105]',
					text: '全部'
				},
				{
					state: '[101]',
					text: '待付款'
				},
				{
					state: '[102]',
					text: '待确认'
				},
				// {
				//   state: '(104)',
				//   text: '待审核'
				// },
				{
					state: '[104,105]',
					text: '历史订单'
				}
			],
			orderList: [],
			buy: '所属矿池',
			state: '待付款',
			price: '购买单价',
			numbr: '购买数量',
			pay: '支付金额',
			trading: '交易类型',
			dater: '下单日期',
			shod: true,
			numberList: [],
			length: 6,
			type: 'number',
			passIn: false,
			delShow: false,
			titl: false,
			password: '',
			num: '',
			num1: '',
			num2: '',
			phone: ''
		};
	},
	components: {
		keyboardPackage,
		passwordInput
	},
	onShow() {
		// this.tabCurrentIndex==0
		this.orderSource();
	},
	onLoad() {
		this.orderSource();
	},
	methods: {
		moveHandle:function(e){
			e.preventDefault();
			e.stopPropagation();
		},
		shensu:function(){
			this.titl=false;
		},
		orderSource() {
			const _this = this;
			const state = this.navList[this.tabCurrentIndex].state;
			_this.data_loading = true;
			uni.showLoading({
				title: '加载中...'
			});
			uni.request({
				url: this.url + 'orderall/',
				method: 'GET',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				data: {
					orderstatus: state
				},
				success(res) {
					console.log(res);
					const orderList = (res.data.data || []).map(o => {
						switch (o.type) {
							case 9:
								o.type_text = '矿机买入';
								o.type_text_color = '#FFB931';
								break;
							case 10:
								o.type_text = '矿机卖出';
								o.type_text_color = '#FF3131';
								break;
							case 11:
								o.type_text = '矿力买入';
								o.type_text_color = '#3D97D6';
								break;
							case 12:
								o.type_text = '矿力转让';
								o.type_text_color = '#7466FF';
								break;
							default:
								o.type_text = '';
								o.type_text_color = '#888';
								break;
						}
						switch (o.status) {
							case 101:
								o.state_text = '待付款';
								break;
							case 102:
								o.state_text = '待确认';
								break;
							case 103:
								o.state_text = '待确认';
								break;
							case 104:
								o.state_text = '已完成';
								break;
							case 105:
								o.state_text = '已取消';
								break;
							default:
								o.state_text = '-';
								break;
						}
						return o;
					});
					console.log(orderList);
					_this.orderList = orderList.reverse();
					uni.hideLoading();
					_this.data_loading = false;
				},
				fail() {
					uni.hideLoading();
					_this.data_loading = false;
				}
			});
		},
		/**
		 * 跳转详情页
		 * @param order
		 */
		handleToDetail(order) {
			if (order.status == 101 && order.type_text == '矿机买入') {
				uni.request({
					url: this.url + 'salemessage/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					data: {
						order_num: order.ordernum
					},
					success(res) {
						console.log(res)
						var ord = JSON.stringify(res.data.data);
						console.log(ord);
						uni.navigateTo({
							url: '../../mill/staypay/staypay?mvvp=' + ord
						});
					}
				});
			}
			if (order.status == 101 && order.type_text == '矿机卖出') {
				uni.request({
					url: this.url + 'salemessage/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					data: {
						order_num: order.ordernum
					},
					success(res) {
						console.log(res)
						var ord = JSON.stringify(res.data.data);
						console.log(ord);
						uni.navigateTo({
							url: '../../mill/machine-sale/machine-sale?mvvp=' + ord
						});
					}
				});
			}
			if (order.status == 102 && order.type_text == '矿机卖出') {
				uni.request({
					url: this.url + 'salemessage/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					data: {
						order_num: order.ordernum
					},
					success(res) {
						var order = JSON.stringify(res.data.data);
						uni.navigateTo({
							url: '../../mill/salepay/salepay?aser=' + order
						});
					}
				});
			}
			if (order.status == 102 && order.type_text == '矿机买入') {
				uni.request({
					url: this.url + 'salemessage/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					data: {
						order_num: order.ordernum
					},
					success(res) {
						var order = JSON.stringify(res.data.data);
						uni.navigateTo({
							url: '../../mill/sale-out/sale-out?aser=' + order
						});
					}
				});
			}
			if (order.type_text == '矿机卖出' && order.status == 104) {
				uni.request({
					url: this.url + 'salemessage/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					data: {
						order_num: order.ordernum
					},
					success(res) {
						var blone = JSON.stringify(res.data.data);
						uni.navigateTo({
							url: '../../mill/completed/completed?deattr=' + blone
						});
					}
				});
			}
			if (order.type_text == '矿机买入' && order.status == 104) {
				uni.request({
					url: this.url + 'salemessage/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					data: {
						order_num: order.ordernum
					},
					success(res) {
						var ksa = JSON.stringify(res.data.data);
						console.log(ksa);
						uni.navigateTo({
							url: '../../mill/salecompleted/salecompleted?cshug=' + ksa
						});
					}
				});
			}
		},
		/**
		 * 订单支付
		 * @param orde
		 */
		onBackPress(option) {
			plus.key.hideSoftKeybord();
		},
		clo: function() {
			this.passIn = false;
			this.$refs['number'].close();
			this.numberList.length = 0;
		},
		cloo: function() {
			this.delShow = false;
			this.$refs['numbers'].close();
			this.numberList.length = 0;
		},
		onDelete() {
			this.numberList.pop();
		},
		onChange1(e) {
			if (e.show == false) {
				this.delShow = false;
			}
			this.numberList.length = 0;
		},
		onChange(e) {
			if (e.show == false) {
				this.passIn = false;
			}
			this.numberList.length = 0;
		},
		onConfirm() {
			if (this.numberList.length != 6) {
				uni.showToast({
					title: '请输入六位交易密码！',
					duration: 2000,
					icon: 'none'
				});
			}
		},
		//点击付款按钮
		handleOrderPay: function(order) {
			var that = this;
			console.log(order.ordernum);
			that.num = order.ordernum;
			that.passIn = true;
			that.$refs['number'].open();
			that.onInput(val);
		},
		onInput(val) {
			var that = this;
			that.numberList.push(val);
			that.password = that.numberList.join().replace(/,/g, '');
			if (that.numberList.length >= that.length) {
				uni.request({
					url: that.url + 'buyaffirm/',
					method: 'POST',
					data: {
						order_num: that.num,
						password: that.password
					},
					header: {
						Authorization: 'JWT' + ' ' + that.global_.token
					},
					success(res) {
						console.log(res);
						if (res.statusCode == 400) {
							that.numberList.pop();
							that.numberList.length = 0;
							that.$refs.wrong.flag = false;
							var n = res.data.data.err_num;
							var s = 5 - n;
							that.$refs.wrong.tip = '剩余' + s + '次机会';
						}
						if (res.statusCode == 401) {
							console.log('未设置资金密码');
							that.passIn = false;
							that.$refs['number'].close();
							uni.showModal({
								title: '提示',
								content: '未设置资金密码',
								showCancel: false,
								cancelText: '',
								confirmText: '确定',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
							// that.shade = true
							// that.stuse = res.statusCode
						}
						if (res.statusCode == 423) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '交易密码已锁定,请在今日24:00后进行交易',
								icon: 'none',
								duration: 2000
							});
						}
						if (res.statusCode == 201) {
							that.success = true;
						}
						if (res.statusCode == 200) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '付款完成',
								duration: 3000
							});
						}
						var page = getCurrentPages().pop();
						if (page == undefined || page == null) return;
						page.onLoad();
					}
				});
				this.numberList.length = 0;
			}
		},

		/**
		 * 取消订单
		 * @param orde
		 */
		handleOrderCancel(order) {
			var that = this;
			uni.request({
				url: this.url + 'ordercancel/',
				method: 'POST',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				data: {
					order_num: order.ordernum
				},
				success(res) {
					if (res.statusCode == 200) {
						uni.showToast({
							title: '取消成功',
							icon: 'none',
							duration: 2000
						});
					} else if (res.statusCode == 400) {
						uni.showToast({
							title: '该订单不可删除',
							icon: 'none',
							duration: 2000
						});
					}
					that.orderSource();
				}
			});
		},
		/**
		 * 订单确认收款
		 * @param ord
		 */
		onInput1(val) {
			var that = this;
			console.log(that.num1);
			that.numberList.push(val);
			that.password = that.numberList.join().replace(/,/g, '');
			if (that.numberList.length >= that.length) {
				uni.request({
					url: that.url + 'saleaffirm/',
					method: 'POST',
					data: {
						order_num: that.num1,
						password: that.password
					},
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success(res) {
						if (res.statusCode == 400) {
							that.numberList.pop();
							that.numberList.length = 0;
							that.$refs.wrong.flag = false;
							var n = res.data.data.err_num;
							var s = 5 - n;
							that.$refs.wrong.tip = '剩余' + s + '次机会';
						}
						if (res.statusCode == 423) {
							that.delShow = false;
							that.$refs['numbers'].close();
							uni.showToast({
								title: '交易密码已锁定,请在今日24:00后进行交易',
								icon: 'none',
								duration: 2000
							});
						}
						if (res.statusCode == 201) {
							that.success = true;
						}
						if (res.statusCode == 200) {
							that.delShow = false;
							that.$refs['numbers'].close();
							uni.showToast({
								title: '收款完成',
								duration: 2000
							});
						}
						var page = getCurrentPages().pop();
						if (page == undefined || page == null) return;
						page.onLoad(that.val);
					}
				});
				this.numberList.length = 0;
			}
		},
		handleOrderConfirm: function(order) {
			this.num1 = order.ordernum;
			console.log(this.num1);
			this.delShow = true;
			this.$refs['numbers'].open();
			this.onInput1(val);
		},
		/**
		 * 订单申诉
		 * @param order
		 */

		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
		},
		tabClick(index) {
			var that = this;
			that.tabCurrentIndex = index;
			that.orderList = [];
			this.orderSource();
		},
		getPhoneValue: function(e) {
			this.phone = e.detail.value;
			console.log(this.phone);
		},
		handleOrderAppeal(order) {
			this.num2 = order.ordernum;
			console.log(order.ordernum);
			this.titl = true;
			this.arr = [];
		},
		deer: function() {
			var that = this;
			if(that.phone==''){
				uni.showToast({
					title: '填写内容不能为空',
					icon: 'none'
				});
				return false
			}
			uni.request({
				url: this.url + 'orderappeal/',
				method: 'POST',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				data: {
					info: that.phone,
					order_num: that.num2
				},
				success(res) {
					console.log(that.phone);
					 if (res.statusCode == 200) {
						that.titl = false;
						uni.showToast({
							title: '已提交'
						});
					} else if (res.statusCode == 400) {
						uni.showToast({
							title: '该订单不可申诉',
							icon: 'none'
						});
						that.titl = false;
					}
				}
			});
		}
	}
};
